import * as React from "react"

// styles
const pageStyles = {
  color: "#232129",
  padding: 96,
  fontFamily: "-apple-system, Roboto, sans-serif, serif",
}
const headingStyles = {
  marginTop: 0,
  marginBottom: 64,
  maxWidth: 320,
  fontSize: 24,
}
const headingAccentStyles = {
  color: "#A1CAAE",
  fontSize: 48,
  marginBottom: 48,
}
const paragraphStyles = {
  marginBottom: 48,
}
const codeStyles = {
  color: "#8A6534",
  padding: 4,
  backgroundColor: "#FFF4DB",
  fontSize: "1.25rem",
  borderRadius: 4,
}
const listStyles = {
  marginBottom: 96,
  paddingLeft: 0,
}
const listItemStyles = {
  fontWeight: 300,
  fontSize: 24,
  maxWidth: 560,
  marginBottom: 30,
}

const linkStyle = {
  color: "#8954A8",
  fontWeight: "bold",
  fontSize: 22,
  verticalAlign: "5%",
}

const docLinkStyle = {
  ...linkStyle,
  listStyleType: "none",
  marginBottom: 24,
}

const descriptionStyle = {
  color: "#232129",
  fontSize: 14,
  marginTop: 10,
  marginBottom: 0,
  lineHeight: 1.25,
}

// data
const information = [
  {
    text: "How Slay?",
    description:
      "too slay.",
    color: "#FFC0CB",
},
]

// markup
const IndexPage = () => {
  return (
    <main style={pageStyles}>
      <title>Home Page</title>
      <h1 style={headingStyles}>
        Natalie (bestie)
        <br />
        <span style={headingAccentStyles}>is the Slayest 👑</span>
      </h1>
      <p style={paragraphStyles}><i>
        thank you for all the slay {"<3"}
        </i>
      </p>
      <ul style={listStyles}>
        {information.map(link => (
          <li key={link.url} style={{ ...listItemStyles, color: link.color }}>
            <span>
              <a
                style={linkStyle}
              >
                <i>{link.text}</i>
              </a>
              <p style={descriptionStyle}>{link.description}</p>
            </span>
          </li>
        ))}
      </ul>
      <img
        alt="❤️"
      />
      <p style={descriptionStyle}>
      <i><b>
      ily bestie
      </b></i>
      </p>
    </main>
  )
}

export default IndexPage
